.container-header {
  background-image: url("../img/web.jpg");
  background-size: cover;
  opacity: 0.9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
}
.header-box {
  background: rgba(0, 0, 0, 0.65);
  padding: 1rem;
  color: white;
  border-radius: 1rem;
  min-width: 60vw;
  max-width: 70vw;
  margin: 1rem 0;
  max-height: 85vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.flex-item-title {
  font-family: Roboto;
  font-weight: 100;
  text-shadow: 3px 3px grey;
  padding: 0.5rem;
  margin: 0;
  -ms-flex-pack: center;
      justify-content: center;
}
@media only screen and (max-width: 599px) {
  .flex-item-title {
    font-size: 3rem;
  }
}
@media only screen and (min-width: 600px) and (max-width: 899px) {
  .flex-item-title {
    font-size: 4rem;
  }
}
@media only screen and (min-width: 900px) and (max-width: 1201px) {
  .flex-item-title {
    font-size: 5.3rem;
  }
}
@media only screen and (min-width: 1200px) and (max-width: 1799px) {
  .flex-item-title {
    font-size: 5.5rem;
  }
}
@media only screen and (min-width: 1800px) {
  .flex-item-title {
    font-size: 12rem;
  }
}
.flex-item-subtitle {
  text-align: center;
  font-weight: 50;
  margin: 0;
}
@media only screen and (max-width: 599px) {
  .flex-item-subtitle {
    font-size: 0.7rem;
    line-height: 1.4rem;
  }
}
@media only screen and (min-width: 600px) and (max-width: 899px) {
  .flex-item-subtitle {
    font-size: 1rem;
    line-height: 1.8rem;
    padding: 0.8rem;
  }
}
@media only screen and (min-width: 900px) and (max-width: 1201px) {
  .flex-item-subtitle {
    font-size: 1.5rem;
    padding: 0.8rem;
    line-height: 2rem;
  }
}
@media only screen and (min-width: 1200px) and (max-width: 1799px) {
  .flex-item-subtitle {
    font-size: 1.4rem;
    padding: 0.5rem;
    line-height: 2.7rem;
  }
}
@media only screen and (min-width: 1800px) {
  .flex-item-subtitle {
    font-size: 3rem;
    padding: 0.8rem;
    line-height: 3.5rem;
    top: 4rem;
  }
}
h4 {
  margin: 0;
  font-weight: 50;
}
.flex-item-button {
  padding: 0.8rem;
}
@media only screen and (min-width: 900px) and (max-width: 1201px) {
  .flex-item-button {
    padding: 1.5rem;
  }
}
@media only screen and (min-width: 1200px) and (max-width: 1799px) {
  .flex-item-button {
    padding: 2rem;
  }
}
@media only screen and (min-width: 1800px) {
  .flex-item-button {
    padding: 2.5rem;
  }
}
.button {
  background-color: rgba(255, 255, 255, 0.2);
  border-width: 0rem;
  border-color: #656B56;
  border-radius: 0.5rem;
  color: white;
  padding: 1rem 1.618rem;
  text-align: center;
  text-decoration: none;
  font-weight: lighter;
}
@media only screen and (max-width: 599px) {
  .button {
    font-size: 0.9rem;
  }
}
@media only screen and (min-width: 600px) and (max-width: 899px) {
  .button {
    font-size: 0.9rem;
  }
}
@media only screen and (min-width: 900px) and (max-width: 1201px) {
  .button {
    font-size: 1.2rem;
  }
}
@media only screen and (min-width: 1200px) and (max-width: 1799px) {
  .button {
    font-size: 1.5rem;
  }
}
@media only screen and (min-width: 1800px) {
  .button {
    font-size: 3.5rem;
  }
}
.button:hover {
  cursor: -webkit-grab;
  cursor: grab;
  background-color: rgba(255, 255, 255, 0.8);
  color: #A55F45;
  -webkit-transition: all 0.8s;
  -o-transition: all 0.8s;
  transition: all 0.8s;
}



